<script lang="ts">import { WeekDays } from "../models/DayModel";
export let value = [];
const days = WeekDays;
const toggleDay = day => {
    const isInArray = value.includes(day.id);
    if (isInArray) {
        value = value.filter(v => v !== day.id);
    }
    else {
        value = [...value, day.id];
    }
};
</script>

<style lang="scss">main {
  display: flex;
  justify-content: space-between; }
  main .day {
    border-radius: 50%;
    font-weight: 700;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; }
    main .day:active, main .day.selected {
      background-color: #828282; }
/*# sourceMappingURL=src/components/WeekSelector.svelte.map */</style>

<main>
  {#each days as day (day.id)}
    <div
      class="day"
      class:selected={value.includes(day.id)}
      on:click={() => toggleDay(day)}>
      {day.symbol}
    </div>
  {/each}
</main>
