<script lang="ts">import { onMount } from "svelte";
export let icon = "";
export let globalClass = "";
let svgIcon = "";
onMount(() => {
    const currentClass = /class=".*"/.exec(icon);
    globalClass = "svelte-svg " + globalClass;
    if (!currentClass) {
        svgIcon = icon.replace("<svg", `<svg class="${globalClass}"`);
    }
    else {
        svgIcon = icon.replace('class="', `class="${globalClass} `);
    }
});
</script>

<style lang="scss">:global(svg.svelte-svg).white {
  fill: white; }

:global(svg.svelte-svg).black {
  fill: black; }
/*# sourceMappingURL=src/components/Svg.svelte.map */</style>

{@html svgIcon}
